import { initializeApp } from 'firebase/app';
import { getStorage, ref, uploadBytesResumable } from "firebase/storage";
import Preview from './upload.js';


const firebaseConfig = {
	apiKey: "AIzaSyCCPjDXzS-Je3ggL2nuqX8DUq6U0qkpEVc",
	authDomain: "image-pr.firebaseapp.com",
	projectId: "image-pr",
	storageBucket: "image-pr.appspot.com",
	messagingSenderId: "326248839668",
	appId: "1:326248839668:web:33f34daa552b84292da1a1"
};

const firebaseApp = initializeApp(firebaseConfig);
const storage = getStorage(firebaseApp);

const imgPreview = new Preview('.file', {
	multiple: true,
	type: ['.png', '.jpg', '.jpeg', '.svg', '.gif', '.ico', '.webp'],
	onUpload(files, progress) {
		files.forEach((file, index) => {
			const fileRef = ref(storage, `images/${file.name}`)
			const task = uploadBytesResumable(fileRef, file)

			task.on('state_changed', snapshot => {
				const proceent = ((snapshot.bytesTransferred / snapshot.totalBytes) * 100).toFixed(0) + '%';
				console.log(proceent)
				const progressBar = progress[index].querySelector('.preview-bar')
				progressBar.style.width = proceent
				progressBar.textContent = proceent
			},
				error => {
					console.log('Error: ', error)
				},
				complete => {
					console.log('complete upload')
				})
		})

	}
});