export default class Preview {
	constructor(selector, options) {
		this.input = document.querySelector(selector);
		this.multiple = options.multiple;
		this.type = options.type
		this.onUpload = options.onUpload || this.noop;
		this.arrFiles = [];

		this.container = document.createElement('div');
		this.container.classList.add('preview')
		this.open = document.createElement('button');
		this.open.classList.add('btn');
		this.open.textContent = 'Open';
		this.close = document.createElement('button');
		this.close.classList.add('btn');
		this.close.classList.add('btn-primary');
		this.close.textContent = 'Upload';



		this.input.insertAdjacentElement('afterend', this.open);




		this.open.addEventListener('click', () => this.input.click());

		this.checkOptions()

		this.input.addEventListener('change', (e) => {
			this.showPreview(e);
		});

		this.container.addEventListener('click', e => {
			this.deletePreview(e)
		})


	}

	noop() {

	}

	downloadPreview(e) {
		const closeBtn = this.container.querySelectorAll('.preview-close');
		const previewInfo = this.container.querySelectorAll('.preview-info');

		closeBtn.forEach(btn => btn.style.display = 'none');
		previewInfo.forEach(info => {
			info.innerHTML = '';

			info.style.transform = 'translateY(0)'
			info.style.height = '20px'

			const progressBar = document.createElement('div');
			progressBar.classList.add('preview-bar');
			info.insertAdjacentElement('beforeend', progressBar)
		})

		this.onUpload(this.arrFiles, previewInfo)


	}

	showPreview(e) {
		this.open.insertAdjacentElement('afterend', this.container);

		const file = e.target.files;

		this.arrFiles = Array.from(file);

		if (!file.length) {
			return
		}

		this.open.insertAdjacentElement('afterend', this.close);

		this.close.addEventListener('click', e => this.downloadPreview(e))

		this.arrFiles.forEach(arrFile => {

			if (!arrFile.type.match('image')) {
				return;
			}

			const reader = new FileReader();

			this.container.innerHTML = ''
			reader.addEventListener('load', (e) => {




				const dataUrl = e.target.result;

				const previewContainer = document.createElement('div');
				previewContainer.classList.add('preview-item');

				const fileName = arrFile.name.split('.')

				previewContainer.innerHTML =
					`
					<div class="preview-close" data-name="${arrFile.name}">X</div>
					<img class="preview-img" src="${dataUrl}" alt="${arrFile.name}">
					<div class="preview-info">
						<div class="preview-name">${fileName[0]}</div>
						<div class="preview-bite">${this.bytesToSize(arrFile.size)}</div>
					</div>
				`;

				this.container.insertAdjacentElement('beforeend', previewContainer);


			})

			reader.readAsDataURL(arrFile);

		})





	}



	deletePreview(e) {

		let deleteName = e.target;

		if (!deleteName.dataset.name) {
			return
		}

		this.arrFiles = this.arrFiles.filter(file => file.name != deleteName.dataset.name)

		if (this.arrFiles.length === 0) {
			this.close.remove()
			this.container.remove()
		}

		let deleteItem = deleteName.closest('.preview-item');
		deleteItem.remove();
	}


	bytesToSize(bytes) {
		const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB']
		if (bytes === 0) return 'n/a'
		const i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)), 10)
		if (i === 0) return `${bytes} ${sizes[i]})`
		return `${(bytes / (1024 ** i)).toFixed(1)} ${sizes[i]}`
	}

	checkOptions() {
		if (this.multiple === true) {
			this.input.setAttribute('multiple', 'multiple')
		}

		if (this.type && Array.isArray(this.type)) {
			this.input.setAttribute('accept', this.type.join(','))
		}
	}
}
